<template>
    <div class="hbHistory">
        <header>
            <a href="javascript:history.back(-1)">
                <i class="el-icon-arrow-left"></i>
            </a>
            <span>历史红包</span>
        </header>
        <section>
            <ul>
                <li v-for="h in oldHb">
                    <div class="list_item">
                        <div class="list_item_left">
                            <span>￥</span><span>{{h.amount}}</span>
                            <p>满 {{h.sum_condition}} 元可用</p>
                        </div>
                        <div class="list_item_right">
                            <h4>{{h.name}}</h4>
                            <p>{{h.end_date}} 到期</p>
                            <p>限收货手机号为 {{h.phone}}</p>
                        </div>
                        <div class="time_left">已过期</div>
                    </div>
                    <div class="foot">
                        <p>{{h.limit_map?h.limit_map.restaurant_flavor_ids:""}}</p>
                    </div>
                </li>
            </ul>
        </section>

    </div>
</template>

<script>
    export default {
        name: "HbHistory",
        data(){
            return{
                user_id:"",
                oldHb:[],
            }
        },
        created(){
            this.user_id = localStorage.getItem("user_id");
            this.axios.get("/promotion/v2/users/"+this.user_id+"/expired_hongbaos?limit=20&offset=0").then(res=>{
                // console.log(res)
                this.oldHb = res;
            }).catch(err=>{});
        }
    }
</script>

<style scoped>
    .hbHistory{
        width: 100%;
        overflow: hidden;
    }
    a{
        text-decoration: none;
    }
    header>a{
        color: #fff;
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
    }
    header span{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
    section{
        padding: .7rem;
        color: #ccc;
    }
    section li{
        border-top: dashed 0.2rem #ccc;;
        background-size: .5rem .2rem;
        margin-bottom: .5rem;
        border-radius: .25rem;
        background-color: #fff;
    }
    .list_item{
        display: flex;
        justify-content: space-between;
        padding: 1rem .5rem .8rem;
    }
    .list_item_left{
        border-right: .025rem dotted #ccc;
        flex: 1;
    }
    .list_item_left>span:nth-child(1){
        font-size: .75rem;
        color: #999;
        font-weight: 700;
    }
    .list_item_left>span:nth-child(2){
        font-size: 1.5rem;
        color: #999;
    }
    .list_item_left>p{
        margin-top: .2rem;
        font-size: .4rem;
        color: #999;
    }
    .list_item_right{
        flex: 2;
        margin-left: 1.5rem;
    }
    .list_item_right>h4{
        font-size: .7rem;
        color: #666;
        margin-left: -.7rem;
    }
    .list_item_right>p{
        margin-top: 0.2rem;
        margin-left: -.7rem;
        font-size: .4rem;
        color: #999;
        white-space: nowrap;
    }
    .time_left{
        font-size: .75rem;
        color: #999;
    }
    .foot{
        background-color: #f9f9f9;
        padding: .4rem;
        border-bottom-left-radius: .25rem;
        border-bottom-right-radius: .25rem;
    }
    .foot>p{
        font-size: .4rem;
        color: #999;
        margin-left: .4rem;
        line-height: 0.65rem;
    }
</style>